    destroyed() {
    },
    async mounted() {
      let res = await Fetch(http.GetCountByCategory) || {
        data: {
          "危险": 32,
          "警告": 32,
          "注意": 32
        }
      };
      this.left_1_2 = {
        title: {
          theme: "dark",
        },
        legend: {
          type: 'scroll',
          bottom: 5,
          shadowBlur: 10,
          icon: "rect",
          data: res.data.map(v => v.CategoryName),
        },
        color: ["#e02315", "#e0a415", "#3dacf0"],
        series: [
          {
            name: '名称',
            type: 'pie',
            radius: ['50%', '70%'],
            center: ['50%', '45%'],
            data: res.data.map(v => ({
              name:v.CategoryName,
              value: v.Count,
            })),
            itemStyle: {
              shadowColor: 'rgba(0, 0, 0, .5)',
              shadowBlur: 10,
            },
            label: {
              show: false,
              position: 'center',
              formatter: '{c} 种',
              rich: {
                rank: {
                  color: 'white',
                },
              },
            },
            labelLine: {
              show: false
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '20',
                lineHeight: 30
              }
            },
          }
        ]
      }
      res = await Fetch(http.GetRiskPointCount);
      this.left_2_1 = res.data;
      res = await Fetch(http.RiskCompany);
      this.left_2_2 = res.data.map(item => ({
        ...item,
        value:item.OrgCode,
        percentage: (item.OrgCode / 500).toFixed(2)
      }));
      this.left_2_2_value = this.left_2_2[0]["CompanyName"] || ""
      res = await Fetch(http.PollutantDischargeData);
      this.right_2_1 = {
        title: {
          theme: "dark",
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}  {c} ({d}%)'
        },
        color: ["#e66f6f", "#e0a415", "#e02315", "#cb6f16", "#3dacf0", "#15e040", "#1af1a9"],
        series: [
          {
            name: '名称',
            type: 'pie',
            radius: '80%',
            center: ['45%', '50%'],
            data: res.data.map(v => ({name: v.Key, value: v.Val})),
            itemStyle: {
              shadowColor: 'rgba(0, 0, 0, .5)',
              shadowBlur: 10,
            },
            label: {
              show: false,
            }
          }
        ]
      };

      res = await Fetch(http.GetPollutantCountByCategory);
      this.right_2_1_1 = {
        title: {
          theme: "dark",
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}  {c} ({d}%)'
        },
        color: ["#e66f6f", "#e0a415", "#e02315", "#cb6f16", "#3dacf0", "#15e040", "#1af1a9"],
        series: [
          {
            name: '名称',
            type: 'pie',
            radius: '80%',
            center: ['45%', '50%'],
            data: res.data.map(v => ({name: v.Key, value: v.Val})),
