      res = await Fetch(http.ComapnyRegCount);
      let colors = [
        "#e02315",
        "#e0a415",
        "#15e040",
        "#1af1a9"
      ]
      let right_2_2 = res.data.map(v => ({
        name: v.Key,
        value: v.Val,
      })).sort((x, y) => y.value - x.value)

      for (let i = 0; i < right_2_2.length; i++) {
        let x = i
        if (x > colors.length - 1) {
          x = colors.length - 1
        }
        right_2_2[i].itemStyle = {
          normal: {
            show: true,
            color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
              offset: 0,
              color: "rgba(255,255,255)"
            }, {
              offset: 1,
              color: colors[x]
            }], false),
            barBorderRadius: 10
          },
          emphasis: {
            shadowBlur: 15,
            shadowColor: 'rgba(0, 0, 0, 0.1)'
          }
        }
      }
      this.right_2_2 = {
        grid: {
          left: '20px', // 距离dom间距
          right: '20px',
          top: '4%',
          bottom: '0%',
          containLabel: true,
        },
        xAxis: [{
          type: "value",
          // 横坐标 分割线等取消显示
          axisTick: {
            show: false
          },
          axisLine: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLabel: {
            show: false,
            verticalAlign: "middle",
            align: "right",
          }
        }],
        yAxis: [
          {
            type: "category",
            inverse: true,
            axisLabel: {
              show: false,
            },
            yAxisIndex: 0,
            axisTick: {
              show: false
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            }
          },
        ],
        series: [
          {
            type: "bar",
            barWidth: "10%",
            label: {
              show: true,
              position: [305, '-15px'],
              textStyle: {
                fontSize: 16
              },
              formatter: (value) => {
                let index = value.dataIndex + 1
                if (index > colors.length - 1) {
                  index = 5
                }
                return `{rank${index}|${value.name}}`
              },
              rich: {
                rank1: {
                  color: colors[0],
                },
                rank2: {
                  color: colors[1],
                },
                rank3: {
                  color: colors[2],
                },
                rank4: {
                  color: colors[3],
                },
                rank5: {
                  color: "#aaaaaa",
                },
              },
            },
            data: right_2_2.map(v => [100, v.value]),
            z: 1,
            barGap: '-100%',
            itemStyle: {
              normal: {
                color: 'transparent',
                borderColor: 'rgba(26, 241, 169, 0.1)',
                borderWidth: '1',
              },
            },
            legendHoverLink: true
          },
          {
            type: "bar",
            barWidth: "10%",
            label: {
              show: true,
              position: [0, '-15px'],
              textStyle: {
                fontSize: 16
              },
              width: "100%",
              formatter(value) {
                return `{rank1|${value.name}}`
              },
              rich: {
                rank1: {
                  color: 'white',
                },
              },
            },
            data: right_2_2,
            z: 2,
            legendHoverLink: true
          }
        ]
      };
    },
    data() {
      return {
        left_1_2: {},
        left_2_1: [],
        left_2_2: [],
        left_2_2_value: "",
        right_1_value: 1,
        right_1_1: G.genCompanys(5).map(v => ({
          company: v,
        })),
        right_1_2: G.genCompanys(5).map(v => ({
          company: v,
        })),
        right_1_3: G.genCompanys(5).map(v => ({
          company: v,
        })),
        right_2_1: {},
        right_2_1_1: {},
        right_2_2: {},
      }
    },
    computed: {
      left_2_2_option: function () {
        let current = R.find(R.propEq("CompanyName", this.left_2_2_value))(this.left_2_2)
        if(!current) return {};
        return {
          series: [{
            type: 'liquidFill',
            radius: '90%',
            waveAnimation: true,
            data: [{
              value: current.percentage,
              direction: 'left',
              itemStyle: {
                normal: {
                  color: 'rgba(26,241,169,1)'
                }
              }
            }, {
              value: current.percentage,
              direction: 'left',
              itemStyle: {
                normal: {
                  color: 'rgba(26,241,169,.6)'
                }
              }
            }, {
              value: current.percentage,
              direction: 'left',
              itemStyle: {
                normal: {
                  color: 'rgba(26,241,169,.3)'
                }
              }
            }],
            outline: {
              // show: true , //是否显示轮廓 布尔值
              borderDistance: 0, // 外部轮廓与图表的距离 数字
              itemStyle: {
                borderColor: 'rgba(26,241,169,1)', // 边框的颜色
                borderWidth: 2, // 边框的宽度
                shadowBlur: 5, //外部轮廓的阴影范围 一旦设置了内外都有阴影
                shadowColor: '#000' //外部轮廓的阴影颜色
              }
            },
            itemStyle: {
              opacity: 0.9, // 波浪的透明度
              shadowBlur: 0 // 波浪的阴影范围
            },
            backgroundStyle: {
              color: 'rgba(26,241,169,.1)' // 图表的背景颜色
            },
            label: { // 数据展示样式
              show: true,
              color: '#fff',
              insideColor: '#fff',
              fontSize: 48,
              fontFamily: "fx-led",
              fontWeight: 400,
              align: 'center',
              baseline: 'middle',
              position: 'inside',
              formatter: function () {
                return current.value
              }
            }
          }]
        }
      }
    },
    methods: {}
  }
</script>

